*, :before, :after {
  box-sizing: inherit;
}

:root {
  --bgColor: white;
  --color: #222;
}

@media (prefers-color-scheme: dark) {
  :root {
    --bgColor: #222;
    --color: white;
  }
}

html {
  box-sizing: border-box;
}

body {
  background-color: var(--bgColor);
  color: var(--color);
  margin: 0;
}

html, body {
  height: 100%;
}

.canvas {
  touch-action: none;
  width: 100%;
  height: 100%;
  display: block;
}
